    /* overflows if normal size, could probably be fixed better */
    font-size: clamp(2vw, 8cqw, 36pt);
}

#why h2 {
    padding: 2pt;
}

#why {
    background-color: var(--blue);
}

#why-content-box {
    place-self: center;
    width: 650px;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
