/*
  Section Contact
*/

#contact {
    background-color: var(--gray);
    min-height: unset; /* This section can be shorter than screen height */
    padding-top: 38px;
    padding-bottom: 38px;
}

#contact p:nth-child(1) {
    margin-top: 0;
}
#contact p, a {
    margin-top: 4px;
}

#contact img {
    width: 250px;
    margin-top: 12px;
    margin-bottom: 32px;
}

#contact-content-box {
    display: flex;
    flex-direction: column;
    place-self: center;
    width: 650px;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
    #contact-content-box {
        place-self: unset;
        width: unset;
        max-width: 650px;
    }
}
